@import url(https://fonts.googleapis.com/css2?family=Raleway:ital,wght@0,400;0,600;0,700;1,400;1,600;1,700&display=swap);
@import url(https://fonts.googleapis.com/icon?family=Material+Icons);
body {
  margin: 0;
  font-family: 'Raleway', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  color:#293241;
}
code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.bg-navbar{
    background-color: #293241;
}
a{
    text-align: center;
}
#brand{
    font-size: xx-large;
}
.brandName{
    font-weight: 600;
    padding-left: 10px;
    font-style: italic;
}

.widget-carrito{
    background-color: #EA526F;
    position: relative;
}
.offcanvas {
    background-color: #293241 !important;
}
.offcanvas-header i{
    color: #EA526F;
}
.nav-item{
    padding: 2rem 3rem;
}
.dropdown-menu{
    background-color: #293241!important;
    border: none;
    padding-top: -10px;
}
.dropdown-item{
    color: #EA526F;
    text-align: center;
    font-size: small;
    padding-top: 0.5rem;
    text-transform: capitalize;
}
@media (min-width: 992px) {
    .nav-item{
        padding: 0.5rem 2rem !important;
    }
}
.greeting{
    font-weight: bold;
    font-style: italic;
}

#hero-brand{
    font-size: 4em;
    color: #EA526F;
}
a{
    text-decoration: none;
    color: #EA526F;
}
.btn{
    border-radius: 50px;
    padding: 10px 13px;
    text-transform: uppercase;
    font-size:medium;
    letter-spacing: 1px;
}
.btn-carrito{
    color: #EA526F;
    border-color: #EA526F;
}
.btn-carrito:hover{
    color: #fff;
    background-color: #EA526F;
}
.btn-carrito:active {
    border-color: #fff;
    background-color: transparent;
    box-shadow: 0 0 0 2px white, 0 0 0 4px #293241;
    color: #293241;
}
.btn-productos{
    color: #fff;
    background-color: #293241;
}

.btn-productos:hover{
    color: #293241;
    border-color: #293241;
    background-color: transparent;
}

.btn-productos:active {
    border-color: #EA526F;
    background-color: #EA526F;
    box-shadow: 0 0 0 2px white, 0 0 0 4px #EA526F;
    color: #fff;
}
.btn-endshop{
    font-size: small;
    padding: .375rem .75rem;
}
textarea, select, input, button { 
    outline: none;
}
.btn-productos:focus { 
    outline-color: transparent; 
    outline-style: none;
}
.btn-trash{
    border: none;
    color:#EA526F;
}
.btn-trash:hover{
    border: none;
    color:#293241;
}
.itemcount{
    display: flex !important;
    justify-content: space-between;
    align-items: baseline;
}
.btncount{
    border-radius: 50%;
    font-size: smaller;
    padding: 5px 10px;
}
.card{
    border:none !important;
}

.card-title h4{
    margin-top: 0px;
}
.card-body{
    padding: 1rem 0 2rem 0;
}
.card span{
    font-weight: bold;
    text-transform: uppercase;
    font-size: small;
}
.products{
    margin: 50px 10px;
    display: flex;
    flex-direction: row;
    justify-content: space-between;
}
.product{
    width: 100%;
    margin-bottom: 75px;
}
.product img{
    padding: 0;
    border-radius: 50px;
}
.productDetail{
    text-align: left;
}
.productDetail img{
    border-radius: 5% !important;
}
.productList{
    padding-left: 0;
}
.productList li{
    list-style: none;
    padding-left: 1rem;
    text-transform: uppercase;
    font-size: small;
    padding-bottom: 0.5rem;
}
.margin{
    margin:0 auto;
}
.margintop{
    margin-top: 1rem;
}
@media (min-width: 576px) {
    .product{
        width: 300px;
    }
}
@media (min-width:768px){
    .container, .container-md, .container-sm{
        max-width: 100%;
    }
    .padding-side{
        padding: 0 20px;
    }
    .margintop{
        margin-top: 0;
    }
}
@media (min-width:992px){
    .container, .container-md, .container-sm{
        max-width: 960px;
    }    
    .countrow{
        position: absolute;
        bottom: 0;
    }
    .card-title h4{
        margin-top: 0px;
    }
    .products{
        margin: 100px 0;
    }
}
@media (min-width:1200px){
    .container, .container-md, .container-sm{
        max-width: 1200px;
    }
    .products{
        margin: 100px;
    }
}
@media (min-width:1400px){
    .container, .container-md, .container-sm{
        max-width: 95%;
    }
    .nav-item p{
        margin-bottom: 0;
    }
}
#no-freeze-spinner {
  position: relative;
  top: 0;
  left: 0;
  transition: all 0.1s linear;
  opacity: 0;
  -webkit-transform: scale(10);
          transform: scale(10);
  z-index: -1;
  background: rgba(0, 0, 0, 0.8);
}
.no-freeze-spinner #no-freeze-spinner {
  z-index: 9999;
  opacity: 1;
  -webkit-transform: scale(1);
          transform: scale(1);
}
#no-freeze-spinner > div > div {
  -webkit-animation-play-state: paused;
          animation-play-state: paused;
  border: 5px solid #293241;
  border-radius: 50%;
  -webkit-animation: rotate 1s infinite linear;
          animation: rotate 1s infinite linear;
  border-left-color: #293241;
  width: 100%;
  height: 100%;
}

#no-freeze-spinner > div {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  margin: 0 auto;
  border-radius: 50%;
  width: 60px;
  height: 60px;
  padding: 5px;
}
.no-freeze-spinner #no-freeze-spinner div div {
  -webkit-animation-play-state: running;
          animation-play-state: running;
}
@-webkit-keyframes loadicons {
  0% {
    -webkit-transform: scale(0) translate3d(0, 0, 0);
            transform: scale(0) translate3d(0, 0, 0);
  }
  11% {
    -webkit-transform: scale(1.2) translate3d(0, 0, 0);
            transform: scale(1.2) translate3d(0, 0, 0);
  }
  22% {
    -webkit-transform: scale(1) translate3d(0, 0, 0);
            transform: scale(1) translate3d(0, 0, 0);
  }
  33% {
    -webkit-transform: scale(0) translate3d(0, 0, 0);
            transform: scale(0) translate3d(0, 0, 0);
  }
}
@keyframes loadicons {
  0% {
    -webkit-transform: scale(0) translate3d(0, 0, 0);
            transform: scale(0) translate3d(0, 0, 0);
  }
  11% {
    -webkit-transform: scale(1.2) translate3d(0, 0, 0);
            transform: scale(1.2) translate3d(0, 0, 0);
  }
  22% {
    -webkit-transform: scale(1) translate3d(0, 0, 0);
            transform: scale(1) translate3d(0, 0, 0);
  }
  33% {
    -webkit-transform: scale(0) translate3d(0, 0, 0);
            transform: scale(0) translate3d(0, 0, 0);
  }
}
@-webkit-keyframes rotate {
  100% {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}
@keyframes rotate {
  100% {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}
.no-freeze-spinner #no-freeze-spinner i {
  font-size: 24px;
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  margin: auto;
  color: #293241;
  width: 24px;
  height: 24px;
  line-height: 1;
  -webkit-transform: scale(0) translate3d(0, 0, 0);
          transform: scale(0) translate3d(0, 0, 0);
}
.no-freeze-spinner #no-freeze-spinner i:nth-of-type(1) {
  -webkit-animation: loadicons 3s infinite ease-in-out;
          animation: loadicons 3s infinite ease-in-out;
}
.no-freeze-spinner #no-freeze-spinner i:nth-of-type(2) {
  -webkit-animation: loadicons 3s 1s infinite ease-in-out;
          animation: loadicons 3s 1s infinite ease-in-out;
}
.no-freeze-spinner #no-freeze-spinner i:nth-of-type(3) {
  -webkit-animation: loadicons 3s 2s infinite ease-in-out;
          animation: loadicons 3s 2s infinite ease-in-out;
}

.alert{
    position: fixed;
    bottom: 0;
    right: 0;
    margin-right: 1rem;
}
.alert p{
    padding: 10px;
    margin: 0;
}
.btn-close{
    width: .25em;
    height: .25em;
}
.cart h3{
    text-align: center;
    padding-top: 1rem;
}
.cart img{
    max-width: 500px;
    margin: 0 auto;
}
.cartlistcontainer{
    text-align: center;
    vertical-align: baseline;
    vertical-align: initial;
}
.cartlistcontainer img{
    width: 50px;
    height: 50px;
    border-radius: 50%;
}
.d-print-none{
    display: none;
}
.centrar{
    margin: 0 auto;display: flex;flex-direction: row;justify-content: center;
}
.form{
    max-width: 600px;
    margin: 0 auto;
}
